@mixin zalpopay-fonts($url: '/common/fonts') {
  @font-face {
    font-family: 'namefont';
    src: local('namefont'), url($url+'/SFUIText-Light.ttf');
    src: local('namefont'), url($url+'/SFUIText-Light.ttf') format('truetype');
    font-weight: 200;
    font-style: normal;
  }
  @font-face {
    font-family: 'namefont';
    src: local('namefont'), url($url+'/SFUIText-Regular.ttf');
    src: local('namefont'), url($url+'/SFUIText-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
  }
  @font-face {
    font-family: 'namefont';
    src: local('namefont'), url($url+'/SFUIText-Medium.ttf');
    src: local('namefont'), url($url+'/SFUIText-Medium.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
  }
  @font-face {
    font-family: 'namefont';
    src: local('namefont'), url($url+'/SFUIText-Semibold.ttf');
    src: local('namefont'),
      url($url+'/SFUIText-Semibold.ttf') format('truetype');
    font-weight: 600;
    font-style: normal;
  }
}
