@import 'colors';

html,
body {
  padding: 0;
  margin: 0;
  background-color: $white;
  min-height: 100%;
  font-family: 'namefont', sans-serif;
  font-size: 15px;
  font-weight: normal;
  font-style: normal;
  line-height: 100%;
}

b {
  font-weight: bold;
}

i {
  font-style: italic;
}

.no-select {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

a {
  outline: none;
  text-decoration: none;

  &:hover {
    text-decoration: none;
  }
}

button,
textarea,
input {
  &:focus {
    outline: none;
  }
}

.container-block {
  position: relative;
  &:before {
    display: table;
    content: ' ';
  }
  &:after {
    display: table;
    content: ' ';
    clear: both;
  }
}

.clearfix:after {
  content: '';
  display: block;
  clear: both;
}
